import mongoose from 'mongoose'
const {Schema} = mongoose

const userSchema = new Schema({


    pseudo:{
        type:String,
        required: true
    },
    email: {
        type: String,
        required: true,
        unique: true
    },
    password: {
        type: String,
        required: true,
       
    },
    avatar:{
        type: String,
        
    },
    admin: {
        type: Boolean,
        default: false
    }
})



export default mongoose.model("User", userSchema)




